.action-button button:after,
.action-button button:before {
  padding: 18px 0 11px;
  content: "";
  position: absolute;
  top: 0;
  left: calc(-100% - 35px);
  height: calc(100% - 29px);
  width: calc(100% + 20px);
  color: #fff;
  border-radius: 2px;
  -webkit-transform: skew(-25deg);
